<script>
    export default{
        name: "NavBar",             
        methods:{
                direcionaLogin () {
                    this.$router.push({ name: 'login'})
                },
                direcionaRegistrar () {
                    this.$router.push({ name: 'novo.usuario'})
                },
                efetuarLogout () {
                    localStorage.removeItem('token')
                    this.$router.push({ name: 'login'})
                    setTimeout(function() {
                        window.location.reload(1);
                    }, 1);
                }
        },
        computed: {
            usuarioestaLogado() {
                return Boolean(localStorage.getItem('token'))                   
            }
        }
    }    
</script>

